@import '../../common/styles/sass/variables';

.table {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 500px;
  background-color: #000;
  overflow-y: auto;

  ul {
    display: flex;
  }

  ul li {
    display: flex;
    justify-content: flex-start;
    width: 170px;
    list-style: none;
    color: $textColor;
  }
}